"use client";

import React, { useState } from "react";
import Link from "next/link";
import { useAuth } from "@/context/AuthContext";
import { FaBars } from "react-icons/fa";

const Sidebar = () => {
  const role  = localStorage.getItem("role");
  const [open, setOpen] = useState(false);

  const adminLinks = [
    { title: "Add Book", link: "/admin/add-book" },
    { title: "View All Orders", link: "/admin/orders" },
  ];

  const userLinks = [
    { title: "Edit Personal Info", link: "/user/edit-info" },
    { title: "My Orders", link: "/user/orders" },
  ];

  const linksToShow = role === "admin" ? adminLinks : role === "user" ? userLinks : [];

  return (
    <div className="flex">
      {/* Sidebar toggle for mobile */}
      <button
        className="md:hidden p-2 m-2 bg-zinc-800 text-white rounded"
        onClick={() => setOpen(!open)}
      >
        <FaBars />
      </button>

      {/* Sidebar */}
      <div
        className={`bg-zinc-900 text-white w-64 p-6 h-screen fixed md:relative top-0 left-0 transform ${
          open ? "translate-x-0" : "-translate-x-full"
        } transition-transform duration-300`}
      >
        <h2 className="text-2xl font-bold mb-6">Dashboard</h2>
        <ul className="flex flex-col gap-4">
          {linksToShow.map((item) => (
            <li key={item.link}>
              <Link
                href={item.link}
                className="block px-4 py-2 rounded hover:bg-yellow-400 hover:text-black transition"
                onClick={() => setOpen(false)}
              >
                {item.title}
              </Link>
            </li>
          ))}
        </ul>
      </div>

      {/* Main content placeholder */}
      <div className="flex-1 ml-0 md:ml-64 p-6">
        {/* Your main page content goes here */}
      </div>
    </div>
  );
};

export default Sidebar;
